@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Arabic&display=swap);
body {
    margin: 0;
    font-family: 'IBM Plex Sans Arabic', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: url(/Button-Component/static/media/3.e75c6bab.jpg) center center;
}
  
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
}
.small-title{
    font-size: 25px;
    font-weight: 600;
    text-align: center;
    margin-bottom: 20px;
    color: #fff;
}
.grid{
    display: grid;
    margin: 0 auto;
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(auto-fill,minmax(300px,1fr));
    padding-bottom: 60px;
}
.grid-item{
    text-align: center;
    background: #fff;
    box-shadow: 5px 3px 10px 0 rgb(34 60 80 / 20%);
    padding: 20px 0;
    transition: all .3s ease;
    border: 1px solid #d3d3d3;
    border-width: 1px 0 0 1px;
}   
.grid-item:hover{
    box-shadow: 5px 3px 12px 5px rgb(163 255 110 / 40%);
}
.grid-item button {
    transition: all .2s ease;
    padding: 8px 16px;
    font-size: 14px;
    text-align: center;
    box-shadow: 0px 2px 3px rgba(51, 51, 51, 0.2);
    border-radius: 6px;
    border: none;
}
.placeholder{
    margin-bottom: 20px;
    font-size: 14px;
    font-weight: 600;
}
.title {
    margin-top: 50px;
    margin-bottom: 20px;
    text-align: center;
    font-weight: bold;
    font-size: 36px;
    color:white;
}
button:hover{
    cursor: default;
    
}
button:disabled{
    background: #E0E0E0;
    color: #9E9E9E;
    pointer-events: none;
}
.default{
    background: #E0E0E0;
    color: #3F3F3F;
    background: #E0E0E0;
}
.default:hover{
    background: #AEAEAE;
    color: #3F3F3F;
}
.outline{
    border: 1px solid #3D5AFE!important;
    background: none;
    color: #3D5AFE;
}
.outline:hover{
    background: rgba(41, 98, 255, 0.1);
    color: #3D5AFE;
}
.text{
    background: none;
    color: #3D5AFE;
    border: none;
    box-shadow: none!important;
}
.text:hover{
    background: rgba(41, 98, 255, 0.1);
}
.text:disabled{
    background: none!important;
}
.disable-shadow{
    box-shadow: none!important;
    background: #3D5AFE;
    color: #fff;
}
.disable-shadow:hover{
    background: #3D5AFE;
}
.sm {
    padding: 6px 12px!important;
}
.lg {
    padding: 11px 22px!important;
}
.color-primary{
    background: #2962FF;
    color: #fff;
}
.color-primary:hover{
    background: #0039CB;
}
.color-secondary {
    background: #455A64;
    color: #fff;
}
.color-secondary:hover{
    background: #1C313A;
}
.color-danger{
    background: #D32F2F;
    color: #fff;
}
.color-danger:hover{
    background: #9A0007;
}
footer {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 40px;
}
footer a {
    color:teal;
    margin: 5px;
}
